let id = ""
export default {
	data() {
		return {
			content: "",
			richTextHeight: 0,
			isShowMore: false,
			articleHeight: 0
		}
	},
	onLoad(options) {
		id = options.id
		this.init()
	},
	async onReady() {
		let timer = setInterval(async () => {
			var size = await this.getElSize('rich')
			if (size.height > 0) {
				clearInterval(timer)
				this.articleHeight = size.height
				if (size.height > this.screenHeight * 1.5) {
					this.richTextHeight = this.screenHeight * 1.5
					this.isShowMore = true
				} else {
					this.richTextHeight = size.height + 30
					this.isShowMore = false
				}
			}
		}, 100)
	},
	methods: {
		init() {
			this.api.home.article.get_detail({
				article_id: id,
				request_type: "app"
			}, res => {
				console.log(res.data)
				this.content = res.data.content
				uni.setNavigationBarTitle({
					title: res.data.title
				});
			})
		},
		getElSize(id) { //得到元素的size
			return new Promise((res, rej) => {
				uni.createSelectorQuery().select("#" + id).fields({
					size: true,
					scrollOffset: true,
					rect: true
				}, (data) => {
					res(data);
				}).exec();
			})
		},
		showAll() {
			this.richTextHeight = this.articleHeight + 30
			this.isShowMore = false
		}
	}
}
